/* Tower Stats Page Styles */
.tower-stats-page {
    background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
    min-height: 100vh;
    margin: 0;
    font-family: 'Winky Sans', sans-serif;
}

.stats-page-wrapper {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.stats-header {
    text-align: center;
    margin-bottom: 40px;
    color: white;
}

.stats-header h1 {
    font-size: 3rem;
    margin: 0 0 20px 0;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.5);
    font-family: 'Metamorphous', serif;
}

.stats-nav a {
    color: #ffd700;
    text-decoration: none;
    font-size: 1.1rem;
    transition: color 0.3s ease;
}

.stats-nav a:hover {
    color: white;
}

.tower-stats-container {
    max-width: 100%;
}

.stats-overview {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    margin-bottom: 40px;
}

.stat-card {
    background: linear-gradient(135deg, #2a1a4a 0%, #432675 100%);
    padding: 20px;
    text-align: center;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
}

.stat-card h3 {
    margin: 0 0 10px 0;
    font-size: 18px;
    color: #fff;
    font-family: 'Metamorphous', serif;
}

.stat-number {
    font-size: 48px;
    font-weight: bold;
    color: #ffd700;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.5);
    font-family: 'Metamorphous', serif;
}

.stats-leaderboards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 30px;
}

.leaderboard {
    background: linear-gradient(135deg, #2a1a4a 0%, #432675 100%);
    padding: 20px;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
}

.leaderboard h3 {
    margin: 0 0 20px 0;
    font-size: 20px;
    color: #fff;
    text-align: center;
    border-bottom: 2px solid rgba(255, 255, 255, 0.3);
    padding-bottom: 10px;
    font-family: 'Metamorphous', serif;
}

.leaderboard-list {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.leaderboard-item {
    display: flex;
    align-items: center;
    padding: 10px;
    background: rgba(255, 255, 255, 0.05);
    transition: background 0.3s ease;
}

.leaderboard-item:hover {
    background: linear-gradient(135deg, #2a1a4a 0%, #432675 100%);
}

.leaderboard a {
    text-decoration: none;
}

.rank {
    font-family: 'Metamorphous', serif;
    font-weight: bold;
    color: #ffd700;
    min-width: 30px;
    margin-right: 15px;
}

.name {
    flex: 1;
    color: #fff;
    font-weight: 500;
}

.count {
    color: #ccc;
    font-size: 14px;
}

@media (max-width: 768px) {
    .stats-page-wrapper {
        padding: 10px;
    }
    
    .stats-leaderboards {
        grid-template-columns: 1fr;
    }
    
    .stat-number {
        font-size: 36px;        
    }
    
    .stats-header h1 {
        font-size: 2rem;
    }
}

